import { defineStore } from "pinia";
import { ref, watch } from "vue";

export const useOSIAppsStore = defineStore('osiapps', () => {
    const LOCALSTORAGE_KEY = "osiapps_store";

    let windows = ref([]);
    const activeWindowId = ref(null);

    let saveTimeout = null; // –°–æ—Ö—Ä–∞–Ω—è–µ–º timeout ID –≤ –æ—Ç–¥–µ–ª—å–Ω–æ–π –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π
    let nextZIndex = 100;

    // –ó–∞–≥—Ä—É–∂–∞–µ–º —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã–µ –æ–∫–Ω–∞ –∏–∑ localStorage + id –∞–∫—Ç–∏–≤–Ω–æ–≥–æ –æ–∫–Ω–∞ + –æ–ø—Ä–µ–¥–µ–ª—è–µ–º zindex
    const loadWindowsFromLS = () => {
        try {
            const saved = localStorage.getItem(LOCALSTORAGE_KEY);
            
            if (saved) {
                const parseData = JSON.parse(saved);

                if (parseData) {
                    // –ø–æ–ª—É—á–∞–µ–º + —Ñ–∏–∫—Å–∏—Ä—É–µ–º –º–∞—Å—Å–∏–≤ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –æ–∫–æ–Ω
                    windows.value = parseData.windows || [];
                    // –ø–æ–ª—É—á–∞–µ–º + —Ñ–∏–∫—Å–∏—Ä—É–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ id –∞–∫—Ç–∏–≤–Ω–æ–≥–æ –æ–∫–Ω–∞
                    activeWindowId.value = parseData.activeWindowId || null;
                    // –≤—ã—á–∏—Å–ª—è–µ–º —Ç–µ–∫—É—â–∏–π –º–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π zindex
                    const maxZIndex = windows.value.reduce((max, w) => 
                        Math.max(max, w.zIndex || 100), 100
                    );
                    // —Ñ–∏–∫—Å–∏—Ä—É–µ–º "—Å–ª–µ–¥—É—é—â–∏–π" zindex
                    nextZIndex = maxZIndex + 1;
                    
                    console.log('Loaded windows from storage:', windows.value.length);
                }
            }
        } catch (error) {
            // –í —Å–ª—É—á–∞–µ –æ—à–∏–±–∫–∏: –∏–Ω—Ñ–æ—Ä–º–∏—Ä—É–µ–º –≤ –∫–æ–Ω—Å–æ–ª—å –∏ –æ—á–∏—â–∞–µ–º localStorage
            console.error('Failed to load OSI apps store:', error);
            localStorage.removeItem(LOCALSTORAGE_KEY);
        }
    };

    // –°–æ—Ö—Ä–∞–Ω—è–µ–º –æ–∫–Ω–∞ –≤ localStorage + id –∞–∫—Ç–∏–≤–Ω–æ–≥–æ –æ–∫–Ω–∞
    const saveWindowsFromLS = () => {
        try {
            const LSState = {
                windows: windows.value.map(w => ({
                    id: w.id,
                    appName: w.appName,
                    contentApp: w.contentApp,
                    title: w.title,
                    isMinimized: w.isMinimized,
                    isMaximized: w.isMaximized,
                    zIndex: w.zIndex,
                    defWidth: w.defWidth,
                    defHeight: w.defHeight,
                    wdata: w.wdata
                })),
                activeWindowId: activeWindowId.value
            };

            localStorage.setItem(LOCALSTORAGE_KEY, JSON.stringify(LSState));
        } catch (error) {
            console.error("Failed to save windows to localstorage:", error);
        }
    };

    // —Å–ª–µ–¥–∏–º –∑–∞ –∏–∑–º–µ–Ω–µ–Ω–∏—è–º–∏ –æ–∫–æ–Ω ("—Å—Ä–∞–∑—É")
    watch(
        windows,
        (newWindows) => {
            console.log(newWindows);
            
            clearTimeout(saveTimeout);
            
            saveTimeout = setTimeout(() => {
                saveWindowsFromLS();
            }, 500);
        },
        { deep: true }
    );

    // –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è local store
    loadWindowsFromLS();
  
    // –∞–∫—Ç–∏–≤–∞—Ü–∏—è –æ–∫–Ω–∞
    const activateWindow = (windowId) => {
        const window = windows.value.find(w => w.id === windowId);
        
        if (window) {
            activeWindowId.value = windowId;
            windows.value.forEach(w => {
                if (w.id === windowId) {
                    w.zIndex = nextZIndex++;
                }
            });
            
            // –°–æ—Ö—Ä–∞–Ω—è–µ–º –ø—Ä–∏ –∞–∫—Ç–∏–≤–∞—Ü–∏–∏ –æ–∫–Ω–∞
            saveWindowsFromLS();
        }
    };

    // –°–æ—Ö—Ä–∞–Ω–∏—Ç—å —Å–æ—Å—Ç–æ—è–Ω–∏–µ –æ–∫–Ω–∞
    const saveWindowState = (windowId, state) => {
        if (!windowId) {
            console.warn('Cannot save window state: windowId is required');
            return;
        }
        
        let newWindows = [];
        windows.value.forEach(w => {
            if (w.id === windowId) {
                w.wdata = {
                    ...state,
                    lastUpdated: Date.now(),
                };
            }

            newWindows.push(w);
        });

        windows = newWindows;
        
        console.log('State saved for window:', windowId);
    };

    // –ü–æ–ª—É—á–∏—Ç—å —Å–æ—Å—Ç–æ—è–Ω–∏–µ –æ–∫–Ω–∞
    const getWindowState = (windowId) => {
        if (!windowId) {
            console.warn('Cannot get window state: windowId is required');
            return null;
        }
        
        // let resultValue;
        // windows.value.forEach(w => {
        //     if (w.id === windowId) {
        //         resultValue = w.wdata;
        //         return true;
        //     }
        // });
        // return resultValue || null;

        const existingWindow = windows.value.find(w => w.id === windowId );

        if (existingWindow && existingWindow.wdata) {
            return existingWindow.wdata;
        } else {
            return null;
        }        
    };

    // –£–¥–∞–ª–∏—Ç—å —Å–æ—Å—Ç–æ—è–Ω–∏–µ –æ–∫–Ω–∞
    const deleteWindowState = (windowId) => {
        if (!windowId) {
            console.warn('Cannot delete window state: windowId is required');
            return;
        }

        let newWindows = [];

        windows.value.forEach(w => {
            if (w.id === windowId) {
                w.wdata = {};
            }

            newWindows.push(w);
        });

        windows = newWindows;
        console.log('State deleted for window:', windowId);
    };

    // –ü–æ–ª—É—á–∏—Ç—å —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É –ø–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è–º
    const getStats = () => {
        const byAppType = {};

        windows.value.forEach(w => {
            const state = w;
            const type = state.wdata.appType || 'unknown';
            byAppType[type] = (byAppType[type] || 0) + 1;
        });

        return {
            totalWindows: windows.value.length,
            byAppType,
            // lastUpdate: Math.max(...windows.map(id => windowStates.value[id]?.lastUpdated || 0))
        };
        // const windows = Object.keys(windowStates.value);
        // const byAppType = {};
        
        // windows.forEach(windowId => {
        //     const state = windowStates.value[windowId];
        //     const type = state?.appType || 'unknown';
        //     byAppType[type] = (byAppType[type] || 0) + 1;
        // });
        
        // return {
        //     totalWindows: windows.length,
        //     byAppType,
        //     lastUpdate: Math.max(...windows.map(id => windowStates.value[id]?.lastUpdated || 0))
        // };
    };

    const openWindow = (appData) => {
        const appName = appData.name;
        const appTitle = appData.label;
        const contentApp = appData.contentapp;

        // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ —Ç–∞–∫–æ–µ –æ–∫–Ω–æ
        const existingWindow = windows.value.find(w => 
            w.appName === appName && w.contentApp === contentApp
        );
        
        if (existingWindow) {
            // –ï—Å–ª–∏ –æ–∫–Ω–æ —É–∂–µ –µ—Å—Ç—å, –∞–∫—Ç–∏–≤–∏—Ä—É–µ–º –µ–≥–æ
            if (existingWindow.isMinimized) {
                restoreWindow(existingWindow.id);
            } else {
                activateWindow(existingWindow.id);
            }
            return existingWindow.id;
        }
        
        // –°–æ–∑–¥–∞–µ–º –Ω–æ–≤–æ–µ –æ–∫–Ω–æ
        const newWindow = {
            ...appData,
            id: 'window_' + Date.now() + '_' + Math.random().toString(36).substr(2, 9),
            appName: appName,
            contentApp: contentApp,
            title: appTitle,
            isMinimized: false,
            isMaximized: appData.isMaximized || false,            
            zIndex: nextZIndex++,
            defWidth: appData.defWidth || 400,
            defHeight: appData.defHeight || 400,
            wdata: {}
        };
    
        windows.value.push(newWindow);
        activateWindow(newWindow.id);
        
        // –°–æ—Ö—Ä–∞–Ω—è–µ–º –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –æ–∫–Ω–∞
        saveWindowsFromLS();
    
        return newWindow.id;
    };

    const closeWindow = (windowId) => {
        const index = windows.value.findIndex(w => w.id === windowId);
        
        if (index !== -1) {
            windows.value.splice(index, 1);
            
            // –ï—Å–ª–∏ –∑–∞–∫—Ä—ã–ª–∏ –∞–∫—Ç–∏–≤–Ω–æ–µ –æ–∫–Ω–æ, –∞–∫—Ç–∏–≤–∏—Ä—É–µ–º –¥—Ä—É–≥–æ–µ
            if (activeWindowId.value === windowId) {
                activeWindowId.value = windows.value.length > 0 
                    ? windows.value[windows.value.length - 1].id 
                    : null;
            }
            
            // –°–æ—Ö—Ä–∞–Ω—è–µ–º –ø—Ä–∏ –∑–∞–∫—Ä—ã—Ç–∏–∏ –æ–∫–Ω–∞
            saveWindowsFromLS();
        }
    };

    const minimizeWindow = (windowId) => {
        const window = windows.value.find(w => w.id === windowId);
        
        if (window) {
            window.isMinimized = true;
            saveWindowsFromLS();
        }
    };
    
    const restoreWindow = (windowId) => {
        const window = windows.value.find(w => w.id === windowId);
    
        if (window) {
            window.isMinimized = false;
            activateWindow(windowId);
            saveWindowsFromLS();
        }
    };

    const toggleMaximizeWindow = (windowId) => {
        const window = windows.value.find(w => w.id === windowId);

        if (window) {
            window.isMaximized = !window.isMaximized;
            activateWindow(windowId);
            saveWindowsFromLS();
        }
    };

    // –ú–µ—Ç–æ–¥ –¥–ª—è –æ—á–∏—Å—Ç–∫–∏ –≤—Å–µ—Ö –æ–∫–æ–Ω
    const clearAllWindows = () => {
        windows.value = [];
        activeWindowId.value = null;
        localStorage.removeItem(LOCALSTORAGE_KEY);
    };

    const detectBrowser = () => {
        const ua = navigator.userAgent;
        let tem;
        let M = ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];
        
        if (/trident/i.test(M[1])) {
            tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
            return { name: 'IE', version: (tem[1] || '') };
        }
        
        if (M[1] === 'Chrome') {
            tem = ua.match(/\b(OPR|Edge)\/(\d+)/);
            if (tem != null) {
                return { name: tem[1].replace('OPR', 'Opera'), version: tem[2] };
            }
        }
        
        M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
        
        if ((tem = ua.match(/version\/(\d+)/i)) != null) {
            M.splice(1, 1, tem[1]);
        }
        
        return {
            name: M[0],
            version: M[1]
        };
    };

    const getLocalStorageUsage = () => {
        try {
            let totalBytes = 0;
            let entries = [];
            
            for(let i = 0; i < localStorage.length; i++) {
                const key = localStorage.key(i);
                const value = localStorage.getItem(key);
                const keySize = key.length * 2; // UTF-16: 2 –±–∞–π—Ç–∞ –Ω–∞ —Å–∏–º–≤–æ–ª
                const valueSize = value.length * 2;
                const entrySize = keySize + valueSize;
                
                totalBytes += entrySize;
                
                entries.push({
                    key,
                    keySize: `${key.length} —Å–∏–º–≤ (${keySize} –±–∞–π—Ç)`,
                    valueSize: `${value.length} —Å–∏–º–≤ (${valueSize} –±–∞–π—Ç)`,
                    totalSize: `${(entrySize / 1024).toFixed(2)} KB`,
                    valuePreview: value.length > 100 ? value.substring(0, 100) + '...' : value
                });
            }
            
            return {
                total: {
                    bytes: totalBytes,
                    kilobytes: (totalBytes / 1024).toFixed(2),
                    megabytes: (totalBytes / (1024 * 1024)).toFixed(4),
                    entries: localStorage.length
                },
                entries,
                limit: {
                    // –û–±—ã—á–Ω—ã–π –ª–∏–º–∏—Ç –¥–ª—è –±–æ–ª—å—à–∏–Ω—Å—Ç–≤–∞ –±—Ä–∞—É–∑–µ—Ä–æ–≤
                    standard: '5 MB',
                    // –¢–µ–∫—É—â–µ–µ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ –≤ –ø—Ä–æ—Ü–µ–Ω—Ç–∞—Ö (–ø—Ä–∏–º–µ—Ä–Ω–æ)
                    usedPercent: ((totalBytes / (5 * 1024 * 1024)) * 100).toFixed(1)
                }
            };
        } catch (error) {
            console.error('Error calculating localStorage usage:', error);
            return null;
        }
    };

    return {
        windows,
        activateWindow,
        saveWindowState,
        getWindowState,
        deleteWindowState,
        openWindow,
        closeWindow,
        minimizeWindow,
        restoreWindow,
        toggleMaximizeWindow,
        clearAllWindows,
        getStats,
        getLocalStorageUsage,
        detectBrowser
    };
});